#banner {
  width: 100%;
  max-width: 1000;

  .bn-navbar {
    height: auto;
    font-size: 2em;
    padding: 20px;
    background-color: #ffffff;
    border: none;
    width: auto;
    font-family: monospace;
    font-weight: bold;
    display: flex;
    justify-content: space-around;
    flex-shrink: 0;
  }

  .bn-navbar .bn-navbar-ic {
    color: black;
    align-items: flex-start;
    text-align: left;
    flex-grow: 4;

    a {
      font-size: 2em;
      padding: 0px 50px;
      color: black;
    }
  }

  .bn-navbar .bn-navbar-men {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    white-space: nowrap;
    padding: 0px 10px 0px 10px;

    .bn-navbar-li {
      flex-grow: 0.5;
    }
    a {
      color: black;
    }
  }
}
